<template>
  <div @mouseleave="show_menu=false" class="dropdown">
    <input @mouseenter="show_menu=true"
           @focus="show_menu=true"
           @keyup="show_menu=true"
           v-model="keyword"
           :placeholder="placeholder"
           type="search"
           autofocus>
    <div v-if="show_menu && result.length" class="menu">
      <div :key="i" @click="select(row)" class="menu-item" v-for="(row,i) in result">{{row[displayKey]}}</div>
    </div>
  </div>
</template>

<script>
  import api from '../lib/api';

  export default {
    props   : {
      placeholder : {
        default : '',
      },
      api         : {}, // {string|object} 
      list        : { // {Array} [{name: whh, id: 1}, ...]
        default () {
          return [];
        },
      },
      default     : {}, // {mixed} id
      onSelect    : {}, // {Function} 
      primaryKey  : {  // {string} 
        default : 'id',
      },
      displayKey  : { // {string} name
        default : 'name',
      },
    },
    mounted () {
      this.set_default(); // props.default
      this.api_conf = this.parse_api(); // props.api
      let list      = this.list;
      list && (this.result = Object.assign([], this.list)); // 
    },
    data () {
      return {
        api_conf  : {}, // props.api
        result    : [], // 
        keyword   : '', // 
        selected  : {}, // 
        show_menu : false, // 
        timer     : null, // debounce
      };
    },
    methods : {
      /**
       * props.api
       * @return {object}
       */
      parse_api () {
        let api_prop = this.api;

        if (typeof api_prop != 'string')
          return Object.assign({}, api);

        api_prop     = api_prop.split('.');
        let model    = api_prop[ 0 ];
        let property = api_prop[ 1 ];
        property     = property.split(',');

        return {
          model,
          property,
        };
      },

      on_edit(row) {
        if(!row)
          this.selected = {};
        this.selected = row;
      },
      /**
       * 
       */
      reset () {
        this.keyword  = '';
        this.selected = {};
      },

      /**
       * 
       */
      set_default () {
        let key = this.default;
        if (!key) {
          this.reset();
          return;
        }

        let def = this.list.find(row => {
          return row[ this.primaryKey ] == key;
        });
        this.select(def);
      },

      /**
       * 
       * @param row
       */
      select (row) {
        this.show_menu = false;
        this.selected  = row;
        this.keyword   = row[ this.displayKey ];

        if (this.onSelect)
          this.onSelect(row);
      },

      /**
       * 
       * search
       */
      filter () {
        this.result = Object.assign([], this.list);
        this.result = this.result.filter(row => {
          return row[ this.displayKey ].indexOf(this.keyword) !== -1;
        });
      },

      /**
       * 
       * api
       */
      search () {
        let condition = {};

        let property = this.api_conf.property;

        if (!property)
          return;

        property.forEach(prop => {
          condition[ prop ] = this.keyword;
        });

        clearTimeout(this.timer);

        this.timer = setTimeout(() => {
          api(`${this.api_conf.model}/search`, { or : condition })
            .then(r => {
              this.result = r.data;
            });
        }, 300);
      },
    },

    watch : {
      /**
       * 
       */
      keyword () {
        if (this.api)
          this.search();
        else
          this.filter();
      },
      /**
       * 
       */
      default () {
        this.set_default();
      },
    },
  };
</script>

<style scoped>
  .dropdown {
    position: relative;
    display: inline-block;
    background: #fff;
  }

  .selected,
  .menu > * {
    display: block;
    padding: 2px 5px;
  }

  .selected,
  .menu {
    border: 1px solid rgba(0, 0, 0, .1);
  }

  .menu {
    background: #fff;
    z-index: 1;
    width: 100px;
    position: absolute;
    left: 0;
    /*top: 0;*/
  }

  .selected,
  .menu-item {
    cursor: pointer;
  }

  .menu-item:hover {
    background: #ddd;
  }
</style>
